import React from 'react'
import axios from 'axios'


class Newuser extends React.Component{
    constructor(props){
        super(props);

        this.state = {
            newName: "",
            newPassword:"",
        }

    }
    handle(e, name){
        let obj = {};
        obj[name] = e.target.value;
        this.setState(obj); 
    };

    AjaxFunction(){

        if (window.XMLHttpRequest) {            //区别浏览器以及版本
            var xhr = new XMLHttpRequest();
            xhr.open("POST", "/newUser", true);
            xhr.send(null);
        } 
          
        axios.post('/newUser', {
            name: '12',
            password: '121', 
        })
        .then(function(response){
            console.log("response")
        })
        .catch(function(error){
            console.log("error")
        });
    }

    render(){
        return (
            <div>
                <lable for="name">账户</lable>
                <input type="text" id="name" onChange={(e) => {this.handle(e, "newName")}} />
                <br/>
                <br/>
                <lable for="password">密码</lable>
                <input type="text" id="password" onChange={(e) => {this.handle(e, "newPassword")}} />
                <br/>
                <button onClick={this.AjaxFunction()}>提交 </button>
            </div>
        )
    }
}

export default Newuser


