import React, { Component } from 'react';
// import logo from '../logo.svg';
// import './App.css';
import {Link} from 'react-router-dom'
import './static/bootstrap/css/bootstrap.min.css';
// import './static/bootstrap/font-awesome/css/font-awesome.css';
import './static/bootstrap/css/animate.css';
import './static/bootstrap/css/style.css';
// import Forgetpassword from './Forgetpassword'
// import Newuser from './Newuser'


class Login extends Component {
  constructor(props){
    super(props);

    this.state={
      user_name:"",
      user_password:"",
    }
  };

  loginUser(event, key){
    let obj = {};
    obj[key] = event.target.value;
    this.setState(obj);
  };

  render() {
    return (
      <div className="middle-box text-center loginscreen  animated fadeInDown">
        <div>
          <div>
            <h1 className="logo-name">R+</h1>
          </div>
          <h3>欢迎使用 R+</h3>
          <form className="m-t" role="form" action="">

            <div className="form-group">
              <input type="email" className="form-control" placeholder="用户名" required="" onChange={(e)=>{this.loginUser(e, "user_name")}}/>
            </div>
            <div className="form-group">
              <input type="password" className="form-control" placeholder="密码" required="" onChange={(e)=>{this.loginUser(e, "user_password")}}/>
            </div>
            <button type="submit" className="btn btn-primary block full-width m-b" >登 录</button>
  
          </form>
        </div>
        <Link to="/forget"> 忘记密码 | </Link>
        <Link to="/newUser">注册新用户</Link>
      </div>
    );
  }
}

export default Login;  



