import {BrowserRouter as Router, Route } from 'react-router-dom'
import Forgetpassword from './js/Forgetpassword'
import Newuser from './js/Newuser'
// import App from './js/App'
// import {Router} from 'react-router-dom'
const React = require('react');
const ReactDOM = require('react-dom');
require('./css/index.css');
const Login = require('./js/Login').default; 
// const registerServiceWorker = require('./js/registerServiceWorker');


ReactDOM.render((
        <Router>
            <div>
                <Route path="/index" component={Login}/>
                <Route path="/newUser" component={Newuser} />
                <Route path="/forget" component={Forgetpassword} />
            </div>
        </Router>
    ),document.getElementById("root")
)

